<template>
  <div id="app">
    <ul>
      <li v-for="product in products" :key="product.id">
        {{ product }}
      </li>
    </ul>
    <ul>
      <li v-for="mytable in mytables" :key="mytable.id">
        {{ mytable.name }} {{ mytable.quantity }}
        <button @click="mytable.quantity += 1">add</button>
      </li>
    </ul>
    <h2>Total : {{ totalnumber }}</h2>
  </div>
</template>
<script>
export default {
  name: "BlackJack",
  data() {
    return {
      products: ["Boots", "Jack", "Tony"],
      mytables: [
        { quantity: 1, name: "Boots" },
        { quantity: 1, name: "Boots" },
        { quantity: 1, name: "Boots" },
      ],
    };
  },
  totalnumber() {
    return this.mytables.reduce((sum, mytable) => {
      return sum + mytable.quantity;
    }, 0);
  },
};
</script>
