<template>
  <div>
    <button @click="(count += 1), (cardnum += 1)">+1</button>
    <button @click="(count += 0), (cardnum += 1)">+0</button>
    <button @click="(count -= 1), (cardnum += 1)">-1</button>
    <p>Count is: {{ count }}</p>
    <p>realnum is: {{ count / ((message * 52 - cardnum) / 52) }}</p>
    <p>Card in bin is: {{ cardnum }}</p>
    <p>Card in stack is: {{ message * 52 - cardnum }}</p>
    <input v-model="message" placeholder="edit me" />
    <p>Message is: {{ message }}</p>
  </div>
</template>
<script>
export default {
  data() {
    return {
      count: 0,
      cardnum: 0,
      message: 6,
    };
  },
};
</script>
